body {
  background-color: #282c34;
  color: white;
  padding: 40px;
  font-family: Arial;
  text-align: center;
  letter-spacing: 0.6px;
}

button {
  background-color: DodgerBlue;
  border: 2px solid dodgerblue;
  padding: 10px;
  letter-spacing: 1.2px;
  border-radius: 4px;
  font-size: 12px;
  padding: 8px 12px;
  color: white;
  cursor: pointer;
  &:hover {
    background-color: transparent;
    color: dodgerblue;
    transition-duration: 350ms;
  }
}
